<template>
  <div>
    <v-data-table
      v-model="selected"
      :headers="advancedView ? advancedHeaders : pointHeaders"
      :items="device.points"
      class="elevation-1 text-start"
      item-key="uuid"
      show-select
      :footer-props="tableProps.FOOTER_PROPS"
      :itemsPerPage="tableProps.ITEMS_PER_PAGE"
      :search="search"
    >
      <template v-slot:top >
        <v-breadcrumbs :items="dirs" large>
          <v-breadcrumbs-item
            v-bind:class="{ 'cursor-pointer': !item.disabled }"
            slot="item"
            slot-scope="{ item }"
            @click.native="() => clickBreadcrumb(item)"
            :key="item.text">
