          </v-breadcrumbs-item>
        </v-breadcrumbs>
        <v-toolbar flat>
          <v-toolbar-title>LORA NETWORK POINTS</v-toolbar-title>
          <v-divider class="mx-4" inset vertical></v-divider>
          <div class="text-left pa-2">
            <v-btn @click="onClickRefresh" x-small color="primary" class="white--text" >
              Refresh
              <v-icon x-small right dark> mdi-refresh </v-icon>
            </v-btn>&nbsp;
            <v-menu offset-y>
              <template v-slot:activator="{ on, attrs }">
                <v-btn x-small color  ="#009688" class="white--text"  dark v-bind="attrs" v-on="on" >Download<v-icon x-small right dark>mdi-download</v-icon></v-btn>
              </template>
              <v-list>
                <v-list-item link>
                  <v-list-item-title @click="clickDownload(device.points, 'json')">Json</v-list-item-title>
                </v-list-item>
                <v-list-item link>
                  <v-list-item-title @click="clickDownload(device.points, 'excel')">Excel</v-list-item-title>
                </v-list-item>
                <v-list-item link>
                  <v-list-item-title @click="clickDownload(device.points,'csv')">CSV</v-list-item-title>
                </v-list-item>
              </v-list>
            </v-menu>&nbsp;
            <v-btn @click="clickJsonExport(selected)" :disabled="selected.length === 0" x-small color="#009688" class="white--text" >
              Export<v-icon x-small right dark> mdi-export </v-icon>
            </v-btn>&nbsp;
            <v-btn @click="advancedView = !advancedView" x-small color="purple" class="white--text" > Advanced View <v-icon x-small right dark> {{advancedView ? 'mdi-playlist-minus': 'mdi-playlist-plus'}} </v-icon> </v-btn>&nbsp;
          </div>
          <v-spacer></v-spacer>
          <v-text-field v-model="search" append-icon="mdi-magnify" label="Search" single-line hide-details ></v-text-field>
        </v-toolbar>
      </template>

      <template v-slot:item.enable="{ item }">
        <v-checkbox v-model="item.enable" @click="updatePoint(item)"></v-checkbox>
      </template>

      <template v-slot:item.cov_threshold="{ item }">
        <v-edit-dialog :return-value.sync="item.cov_threshold" @save="updatePoint(item)" @cancel="cancelEdit" @open="openEdit" @close="closeEdit" large>
          <b>{{ item.cov_threshold }}</b>
          <template v-slot:input>
            <v-text-field v-model="item.cov_threshold" single-line type='number' min="0" @input="numberChange()"></v-text-field>
          </template>
        </v-edit-dialog>
      </template>

      <template v-slot:item.value_operation="{ item }">
        <v-edit-dialog :return-value.sync="item.value_operation" @save="updatePoint(item)" @cancel="cancelEdit" @open="openEdit" @close="closeEdit" large>
          <b>{{ item.value_operation }}</b>
          <template v-slot:input>
            <v-text-field v-model="item.value_operation" single-line type='string'></v-text-field>
          </template>
        </v-edit-dialog>
      </template>

      <template v-slot:item.value_offset="{ item }">
        <v-edit-dialog :return-value.sync="item.value_round" @save="updatePoint(item)" @cancel="cancelEdit" @open="openEdit" @close="closeEdit" large>
          <b>{{ item.value_offset }}</b>
          <template v-slot:input>
            <v-text-field v-model="item.value_offset" single-line type='number' min="0" @input="numberChange()"></v-text-field>
          </template>
        </v-edit-dialog>
      </template>

      <template v-slot:item.value_round="{ item }">
        <v-edit-dialog :return-value.sync="item.value_round" @save="updatePoint(item)" @cancel="cancelEdit" @open="openEdit" @close="closeEdit" large>
          <b>{{ item.value_round }}</b>
          <template v-slot:input>
            <v-text-field v-model="item.value_round" single-line type='number' min="0" @input="numberChange()"></v-text-field>
          </template>
        </v-edit-dialog>
      </template>

      <template v-slot:item.input_min="{ item }">
        <v-edit-dialog :return-value.sync="item.input_min" @save="updatePoint(item)" @cancel="cancelEdit" @open="openEdit" @close="closeEdit" large>
          <b>{{ item.input_min }}</b>
          <template v-slot:input>
            <v-text-field v-model="item.input_min" single-line type='number' min="0" @input="numberChange()"></v-text-field>
          </template>
        </v-edit-dialog>
      </template>

      <template v-slot:item.input_max="{ item }">
        <v-edit-dialog :return-value.sync="item.input_max" @save="updatePoint(item)" @cancel="cancelEdit" @open="openEdit" @close="closeEdit" large>
          <b>{{ item.input_max }}</b>
          <template v-slot:input>
            <v-text-field v-model="item.input_max" single-line type='number' min="0" @input="numberChange()"></v-text-field>
          </template>
        </v-edit-dialog>
      </template>

      <template v-slot:item.scale_min="{ item }">
        <v-edit-dialog :return-value.sync="item.scale_min" @save="updatePoint(item)" @cancel="cancelEdit" @open="openEdit" @close="closeEdit" large>
          <b>{{ item.scale_min }}</b>
          <template v-slot:input>
            <v-text-field v-model="item.scale_min" single-line type='number' min="0" @input="numberChange()"></v-text-field>
          </template>
        </v-edit-dialog>
      </template>

      <template v-slot:item.scale_max="{ item }">
        <v-edit-dialog :return-value.sync="item.scale_max" @save="updatePoint(item)" @cancel="cancelEdit" @open="openEdit" @close="closeEdit" large>
          <b>{{ item.scale_max }}</b>
          <template v-slot:input>
            <v-text-field v-model="item.scale_max" single-line type='number' min="0" @input="numberChange()"></v-text-field>
          </template>
        </v-edit-dialog>
      </template>

      <template v-slot:header.enable="{ item }" >
        {{ toUpperTextKey('enable') }}&nbsp;
        <v-icon x-small @click="openMassEditDialog('enable'), $event.stopPropagation()" color="primary">mdi-pencil</v-icon>&nbsp;
      </template>
      <template v-slot:header.cov_threshold="{ item }" >
        {{ toUpperTextKey('cov_threshold') }}&nbsp;
        <v-icon x-small @click="openMassEditDialog('cov_threshold'), $event.stopPropagation()" color="primary">mdi-pencil</v-icon>&nbsp;
      </template>
      <template v-slot:header.value_operation="{ item }" >
        {{ toUpperTextKey('value_operation') }}&nbsp;
        <v-icon x-small @click="openMassEditDialog('value_operation'), $event.stopPropagation()" color="primary">mdi-pencil</v-icon>&nbsp;
      </template>
      <template v-slot:header.value_round="{ item }" >
        {{ toUpperTextKey('value_round') }}&nbsp;
        <v-icon x-small @click="openMassEditDialog('value_round'), $event.stopPropagation()" color="primary">mdi-pencil</v-icon>&nbsp;
      </template>
      <template v-slot:header.input_min="{ item }" >
        {{ toUpperTextKey('input_min') }}&nbsp;
        <v-icon x-small @click="openMassEditDialog('input_min'), $event.stopPropagation()" color="primary">mdi-pencil</v-icon>&nbsp;
      </template>
      <template v-slot:header.input_max="{ item }" >
        {{ toUpperTextKey('input_max') }}&nbsp;
        <v-icon x-small @click="openMassEditDialog('input_max'), $event.stopPropagation()" color="primary">mdi-pencil</v-icon>&nbsp;
      </template>
      <template v-slot:header.scale_min="{ item }" >
        {{ toUpperTextKey('scale_min') }}&nbsp;
        <v-icon x-small @click="openMassEditDialog('scale_min'), $event.stopPropagation()" color="primary">mdi-pencil</v-icon>&nbsp;
      </template>
      <template v-slot:header.scale_max="{ item }" >
        {{ toUpperTextKey('scale_max') }}&nbsp;
        <v-icon x-small @click="openMassEditDialog('scale_max'), $event.stopPropagation()" color="primary">mdi-pencil</v-icon>&nbsp;
      </template>

      <template v-slot:item.actions="{ item }">
        <v-icon small @click="editItem(item)" color="primary"> mdi-pencil </v-icon>
        <!-- <v-icon small @click="clickDelete(item)" color="error"> mdi-delete </v-icon> -->
      </template>

      <template v-slot:no-data> No data</template>
      <template v-slot:item.created_on="{ item }" >
        {{item.created_on | timezone}}
      </template>
      <template v-slot:item.updated_on="{ item }" >
        {{item.updated_on | timezone}}
      </template>
      <template v-slot:item.ts="{ item }" >
        {{item.ts | timezone}}
      </template>
    </v-data-table>

    <!-- Edit dialog-->
    <v-dialog v-model="dialog" max-width="500px">
      <v-card>
        <v-card-title>
          <span class="headline">Edit</span>
        </v-card-title>
        <v-card-text>
          <v-container>
            <v-form v-model="valid" ref="form" lazy-validation>
              <v-row>
                <v-col cols="12" sm="24" md="24">
                  <v-text-field v-model="device.name" disabled class="centered-input"></v-text-field>
                </v-col>
                <v-col cols="12" sm="4" md="4">
                  <v-text-field v-model="editedItem.name" label="Point name" required :rules="[() => !isExistingvalue(editedItem.name, 'name') || 'Name Existed',rules.required]" ></v-text-field>
                </v-col>
                <v-col cols="12" sm="4" md="4">
                  <v-checkbox v-model="editedItem.enable" label="Enable"></v-checkbox>
                </v-col>
                <v-col cols="12" sm="4" md="4">
                  <v-text-field v-model="editedItem.value_operation" label="math operation" > </v-text-field>
                </v-col>
                <v-col cols="12" sm="4" md="4">
                  <v-text-field v-model="editedItem.cov_threshold" :label="toUpperTextKey('cov_threshold')" type="number" min="0" @input="numberChange()" ></v-text-field>
                </v-col>
                <v-col cols="12" sm="4" md="4">
                  <v-text-field v-model="editedItem.value_round" :label="toUpperTextKey('value_round')" type="number" min="0" @input="numberChange()" ></v-text-field>
                </v-col>
                <v-col cols="12" sm="4" md="4">
                  <v-text-field v-model="editedItem.input_min" :label="toUpperTextKey('input_min')" type="number" min="0" @input="numberChange()" ></v-text-field>
                </v-col>
                <v-col cols="12" sm="4" md="4">
                  <v-text-field v-model="editedItem.input_max" :label="toUpperTextKey('input_max')" type="number" min="0" @input="numberChange()" ></v-text-field>
                </v-col>
                <v-col cols="12" sm="4" md="4">
                  <v-text-field v-model="editedItem.scale_min" :label="toUpperTextKey('scale_min')" type="number" min="0" @input="numberChange()" ></v-text-field>
                </v-col>
                <v-col cols="12" sm="4" md="4">
                  <v-text-field v-model="editedItem.scale_max" :label="toUpperTextKey('scale_max')" type="number" min="0" @input="numberChange()" ></v-text-field>
                </v-col>
              </v-row>
            </v-form>
          </v-container>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="darken-1" text @click="close"> Cancel </v-btn>
          <v-btn color="blue darken-1" text @click="save()" :disabled="!valid" > Save </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <!-- Mass Edit dialog-->
    <v-dialog v-model="dialogMassEdit" width="300px">
      <v-card>
        <v-card-title>Mass Edit</v-card-title>
        <v-card-text>
          <v-checkbox v-if="typeof editAll[editField] === 'boolean'" v-model="editAll[editField]" :label="toUpperTextKey(editField)"/>
          <v-text-field v-if="typeof editAll[editField] === 'number'" v-model.number="editAll[editField]" min="0" :label="toUpperTextKey(editField)" type="number" @input="numberChange()"></v-text-field>
          <v-text-field v-if="typeof editAll[editField] === 'string'" v-model="editAll[editField]" :label="toUpperTextKey(editField)" ></v-text-field>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="darken-1" text @click="dialogMassEdit = false">Close</v-btn>
          <v-btn color="primary darken-1" text @click="saveMassEdit(editAll[editField], editField)">Save</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

  </div>
</template>

<script>
import axios from "../../axios";
import * as xlsx from "xlsx";
import _cloneDeep from 'lodash/cloneDeep';
import _find from 'lodash/find';
import * as changeCase from "change-case";
import { DATA_TABLE_PROPS } from '../../constants/appConstant';
import { RULES, POINT_HEADERS, POINT_ADVANCED_HEADERS, POINT_EDIT_ALL_DEFAULT } from './loraConstant';
import { copyToClipboard, exportJSON, exportCSV, exportExcel, toUpperTextKey } from "../../utils/utils";

export default {
  data: () => ({
    toUpperTextKey: toUpperTextKey,
    rules: {...RULES},
    tableProps: {...DATA_TABLE_PROPS},
    pointHeaders: [...POINT_HEADERS],
    advancedHeaders: [...POINT_ADVANCED_HEADERS],
    editAll: {...POINT_EDIT_ALL_DEFAULT},
    dialog: false,
    dialogMassEdit: false,
    device: {},
    selected: [],
    dirs: [],
    file: null,
    search:"",
    valid: false,
    editedIndex: -1,
    editedItem: {},
    editField: "",
    intervalId: null,
    advancedView: false,

  }),
  computed: {
    setting() {
      return this.$store.state.setting;
    },
  },
  watch: {

  },
  beforeRouteLeave (to, from, next) {
    clearInterval(this.intervalId);
    next();
  },
  mounted() {
    this.getItems();

    //set auto refresh
    if(this.setting.refreshTime &&  this.setting.enableAutoRefresh) {
      this.intervalId = setInterval(()=>{
        this.getItems();
        if(this.setting.toastAutoRefresh){
          this.$toasted.show(`Automatically refresh every ${this.setting.refreshTime}ms`, { type: 'info' });
        }
      }, this.setting.refreshTime);
    }
  },
  methods: {
    async getItems() {
      await axios.get(`lora/device/${this.$route.params.id}`).then((data) => {
        this.device = data;
        this.device.points = data.points.map((item) => {
          const keys = Object.keys(item.point_store);
          if (keys && keys.length > 0) {
            keys.forEach((key) => {
              item = { ...item, [key]: item.point_store[key] };
            });
          }
          delete item.point_store;
          delete item.point_uuid;
          return item;
        });

        //get dir
        if(this.dirs.length != 2){
          this.dirs = [{text: 'Devices', link: 'lora/networks'}],
          this.dirs.push({text: this.device.name, disabled: true})
        }
      });
    },
    clickBreadcrumb(dir) {
      if(!dir.disabled){
        this.$router.push({path: '/lora-network/devices'});
      }
    },
    clickJsonExport(){
      const status = copyToClipboard(JSON.stringify(this.selected));
      const msg = status ? `Copied to clipboard!` : `Failure on copying on clipboard!`;
      const type = status ? `success` : `error`;
      return this.$toasted.show(msg, {type: type});
    },
    clickDownload(array, type){
      if(type === "csv"){
          return exportCSV("LoRa Networks - Points", array);
      } if(type === "excel"){
          return exportExcel("LoRa Networks - Points", array);
      } if(type === "json"){
          return exportJSON("LoRa Networks - Points", JSON.stringify(array));
      }
    },
    updatePoint(point) {
      point.name = this.setting.enableChangeCase ? changeCase[this.setting.caseType](point.name) : point.name;
      axios.patch(`lora/point/${point.uuid}`, point )
        .then((response) => {
          const index = this.device.points.findIndex(p=> p.uuid == point.uuid);
          Object.assign(this.device.points[index], point);
          this.$toasted.show('update success');
        })
        .catch((error) => { this.$toasted.show('update fail', {type: 'error'}); });
    },
    save() {
      if(this.$refs.form.validate()){
        if (this.editedIndex > -1) {
          this.updatePoint(this.editedItem);
        } else {
          // this.createPoint(this.editedItem);
        }
        this.close();
      }
    },
    isExistingvalue(value, fieldName) {
      let points = _cloneDeep(this.device.points);
      if(this.editedIndex !== -1){ //when edit
        points.splice(this.editedIndex, 1);
      }
      return _find(points, {[fieldName]: value}) != null;;
    },
    close() {
      this.dialog = false;
      this.$nextTick(() => {
        this.editedItem = {};
        this.editedIndex = -1;
      });
    },
    editItem(item) {
      this.editedIndex = this.device.points.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialog = true;
    },
    saveMassEdit (value, field) {
      for (const item of this.selected) {
        item[field] = value;
        this.updatePoint(item);
      }
      this.dialogMassEdit = false;
    },
    async openMassEditDialog(fieldName){
      if(this.selected.length < 1){
        return this.$toasted.show("Please select item first!", {type: 'error'});
      }
      this.dialogMassEdit = true;
      this.editField = fieldName;
      this.editAll = {...POINT_EDIT_ALL_DEFAULT};
    },
    numberChange(){
      if(!this.editAll[this.editField])
        return this.editAll[this.editField] = 0;
    },
    cancelEdit() { },
    openEdit() { },
    closeEdit() { },
    onClickRefresh(){
      this.getItems();
      this.$toasted.show(`successfully refresh`, { type: 'success' });
    },
  },
};
</script>
<style scoped>
  .centered-input >>> input {
      text-align: center
  }
  .cursor-pointer{
    cursor: pointer;
    color: #1867c0;
  }
  .background-fault-true{
    background-color: #fbb93e;
  }
  .background-enable-false{
    background-color: #666666;
  }
</style>
